.box {
  width: 25%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 10px;
}
.contactBox {
  display: flexbox;
  justify-content: space-around;
}
.list {
  list-style: none;
}

.listItem {
  color: #444dd4;
}

.button {
  margin-top: 20px;
  font-size: 16px;
  font-weight: 450;
  background-color: #444dd4;
  color: #fafafa;
  border: none;
  border-radius: 5px;
  padding: 10px;
  width: 99%;
  margin: 0 auto;
  outline: none;
  cursor: pointer;
  transition: 0.3s;
}

.button:hover {
  background-color: #3038a7;
}
